@font-face {
  font-family: 'LedDisplay';
  src: url('../../fonts/The Led Display St.ttf');
}

@font-face {
  font-family: 'Pixel';
  src: url('../../fonts/LLPIXEL3.ttf');
}

.studio {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: start;
      align-items: flex-start;
  /* background-image: url('../../images/jodie-walton-502797-unsplash.jpg'); */
  background-image: url('../../images/danny-howe-365401-unsplash.jpg');
  background-size: cover;
  background-attachment: fixed;
  min-height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

.workspace {
  width: 100%;
}

.section-top {
  -ms-flex-align: center;
      align-items: center;  
  display: -ms-flexbox;  
  display: flex;
  -ms-flex-pack: left;
      justify-content: left;
  margin: 0;
  padding: 5px;
  overflow-x: scroll;
  scrollbar-color: #333 #111;
}

.section-bottom {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;  
  -ms-flex-wrap: wrap;  
      flex-wrap: wrap;
  margin: 0;
  padding: 5px;
  overflow-x: scroll;
  scrollbar-color: #333 #111;
}

.browser-warning {
  color: #FFF;
  font-size: 2em;
  background-color: #111;
  width: 100%;
  text-align: center;
  padding: 20px;
}

@media (max-width: 1200px) {
  .section-bottom {
    -ms-flex-pack: left;
        justify-content: left;
  }
}


